import ProjectCard from "./ProjectCard";

const Project = ({Data,show,col=0}) => {
    return (
        <div className={col?`sm:grid sm:grid-cols-${col} gap-4`:""}>
            {Data && Data.slice(0,show).map(Element => (
                <ProjectCard title={Element.title} gif={Element.hoverCover} url={Element.external} cover={Element.cover}/>
            ))}
        </div>
    );
}
 
export default Project;